<template>
	<div id="shopSeting" class="warp">
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{path:'/shop/shopinfo'}">店铺信息</el-breadcrumb-item>
				<el-breadcrumb-item>编辑店铺设置</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<div class="title">
			编辑店铺设置
		</div>
		<section class="shopSeting">
			<div class="setInfo">
				<el-form label-width='110px'>
					<el-form-item class='main ' id='top' label="点餐系统">
						<button type="button" class="start" v-for="(item,index)  in buttons" 
							@click='satrt(index)' :class="{'active':startsystem==index}">{{item}}</button>
						<p class="injection">注：选暂停时，客人手机上会显示为系统升级维护中</p>
					</el-form-item>
					<el-form-item class='main' label='启用支付方式'>
						<el-checkbox-group v-model="pay">
							<el-checkbox-button v-for="payment in Payments" :label="payment.idx" :key="payment.text">
								{{payment.text}}
							</el-checkbox-button>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='main' label='订单付款时间'>
						<el-checkbox-group v-model="timePay">
							<el-checkbox-button v-for="paytimed in Paytime" :label="paytimed.idx " :key="paytimed.text ">
								{{paytimed.text}}
							</el-checkbox-button>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='main' label='销售方式'>
						<el-checkbox-group v-model="dinner">
							<el-checkbox-button v-for="sells in sell" :label="sells.idx " 

:key="sells.text ">{{sells.text}}</el-checkbox-button>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='main' label='餐厅标签'>
						<div v-model='shoplabel'>
							<span class="label" v-for='item in shoplabel'>{{item}}</span>
							<span class="add" @click="addlabel"></span>
						</div>
					</el-form-item>
					<el-form-item class='businessTime' label="营业时间">
						<el-time-picker v-model="value2" :picker-options="{
						      selectableRange: '06:00:00 - 23:59:59'
						    }">
						</el-time-picker>
						<el-time-picker v-model="value3" :picker-options="{
						      selectableRange: '00:00:00 - 23:59:59'
						    }">
						</el-time-picker>
					</el-form-item>
					<el-form-item class='dateSet' label="餐时设置">
						<el-checkbox-group v-model="type">
							<el-checkbox :label="Dinnertime.MORN" name="type">{{Dinnertime.toString(Dinnertime.MORN)}}</el-checkbox>
							<el-time-picker v-model="value4" :picker-options="{
						      selectableRange: '00:00:00 - 23:59:59'
						    }">
							</el-time-picker>
							<el-time-picker v-model="value5" :picker-options="{
						      selectableRange: '00:00:00 - 23:59:59'
						    }">
							</el-time-picker>
							<el-checkbox :label="Dinnertime.NOON" name="type">{{Dinnertime.toString(Dinnertime.NOON)}}</el-checkbox>
							<el-time-picker v-model="value6" :picker-options="{
						      selectableRange: '10:01:00 - 23:59:59'
						    }">
							</el-time-picker>
							<el-time-picker v-model="value7" :picker-options="{
						      selectableRange: '10:01:00 - 23:59:59'
						    }">
							</el-time-picker>
							<el-checkbox :label="Dinnertime.EVEN" name="type">{{Dinnertime.toString(Dinnertime.EVEN)}}</el-checkbox>
							<el-time-picker v-model="value8" :picker-options="{
						      selectableRange: '16:30:00 - 23:59:59'
						    }">
							</el-time-picker>
							<el-time-picker v-model="value9" :picker-options="{
						      selectableRange: '16:31:00 - 23:59:59'
						    }">
							</el-time-picker>
							<el-checkbox :label="Dinnertime.NIGHT" name="type">{{Dinnertime.toString(Dinnertime.NIGHT)}}</el-checkbox>
							<el-time-picker v-model="value10" :picker-options="{
						      selectableRange: '21:00:00 - 06:00:00'
						    }">
							</el-time-picker>
							<el-time-picker v-model="value11" :picker-options="{
						      selectableRange: '03:00:00 - 06:00:00'
						    }">
							</el-time-picker>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='invoice nomain clearfix' label="发票">
						<button type="button" class='noInvoice left' @click='shows' 

:class="{'active':show===Invoce.NO}">{{Invoce.toString(Invoce.NO)}}</button>
						<span class="line"></span>
						<button type="button"  class="paperInvoice" @click='showInvos' 

:class="{'selectd':showinvos===Invoce.PAPERINVOICE}">{{Invoce.toString(Invoce.PAPERINVOICE)}}</button>
						<button type="button"  class="electronicInvoice" @click='addCls' 

:class="{'selectd':showinvs===Invoce.ELECTRONICINVOICE}" >{{Invoce.toString(Invoce.ELECTRONICINVOICE)}}</button>
					</el-form-item>
					<el-form-item class='paperTickets' label="纸票" v-show='showvos'>
						<el-checkbox-group v-model="inType">
							<el-checkbox-button v-for="papers in paper" :label="papers.idx" 

:key="papers.text">{{papers.text}}</el-checkbox-button>
						</el-checkbox-group>
						 
					</el-form-item>
					<el-form-item label="发票备注">
						<el-input type='textarea' v-model='remark' placeholder='备注文字总数不得超过120字'></el-input>
					</el-form-item>
					<el-form-item class='clearfix' label="店铺图片">
						<div class="shopinfo left" v-if="shoppic.length==0">
							<img src="../../img/res.png" alt="" />
						</div>
						<div class="shopinfo left" ref='info'  v-for="(item,index) in shoppic"  v-else>
							<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+item"   alt="" />
							 <div class="bg" ref='bg'></div>
							<i class="el-icon-delete2" @click="delt(index)"></i>
							<i class="el-icon-view" @click="clickImg(index)"></i>
						</div>
						<img-upload @send-image='getShopphoto' :ImageSize='imgSize'></img-upload>
							<div class="text-warp">
								<p class="text top">注：最多可上传五张图片，支持JPG、JPEG、PNG文件格式，大小不得大于1M</p>
							</div>
						
					</el-form-item>
					<div class="bottom">
						<button type="button" class="blue" @click="save">保存</button>
						<button type="button" class="default" @click="goBack">取消</button>
					</div>
				</el-form>
			</div>
		</section>
		 <!-- 餐厅标签编辑弹窗 -->
        <tg
        :dialogVisible="dialogVisible"
        :selectArr="shoplabel"
        :tagObj="labelObj"
        @on-close="hideDialog"
        @on-change='getLabel'
        ></tg>
        <!-- 餐厅标签编辑弹窗 -->
        <!--图片放大弹窗-->
        <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
        <!--图片放大弹窗-->
	</div>
</template>

<script>
	import { shopSeting,getShopinfo } from '../../../api';
	import { Subtime,addZero,Tranobj,timeToArr } from '../../../index';
	import tg  from '../../../../merchandise/components/tagEditor';
	import ImgUpload from '@/components/upload/SUpload';
	import BigImg from '../../../components/showBigImg';
	import { Util } from '@/config/util';
	import { Public } from '@/config/public';
	import { Invoce,Startsystem,Payway,PayTime,Saleway,Restauran,Invoice,Dinnertime } from '@/config/cfg';
	export default {
		components:{
			tg,
			ImgUpload,
			BigImg
		},
		data() {
			return {
				dialogVisible:false,  //控制添加餐厅标签弹窗
				show:Invoce.YES,  //控制是否提供发票
				showinvos:Invoce.NO, //控制显示纸票
				showvos:Invoce.SHOWPAPERINVOICE, //控制显示纸票下的子类(普通，专用)
				showinvs:Invoce.NO, //控制显示电子发票
				buttons: [Startsystem.toString(Startsystem.START),Startsystem.toString(Startsystem.PAUSE)], //显示(启用，暂停)
/*支付方式*/
				pay: [],
				Payments: [{
						text:Payway.toString(Payway.CASHPAYMENT),
						idx: Payway.CASHPAYMENT
					},
					{
						text:Payway.toString(Payway.CARDPAYMENT),
						idx:Payway.CARDPAYMENT
					},
					{
						text: Payway.toString(Payway.ALIPAY),
						idx: Payway.ALIPAY
					},
					{
						text:Payway.toString(Payway.WECHATPAY),
						idx: Payway.WECHATPAY
					}
				],
/*支付方式*/

/*付款时机*/
				timePay: [],
				Paytime: [{
						text:PayTime.toString(PayTime.BEFOREPAY),
						idx: PayTime.BEFOREPAY
					},
					{
						text:PayTime.toString(PayTime.AFTERPAY),
						idx:PayTime.AFTERPAY
					}
				],
/*付款时机*/

/*用餐方式*/
				dinner: [],
				sell: [{
						text:Saleway.toString(Saleway.SHOPEAT),
						idx:Saleway.SHOPEAT
					},
					{
						text:Saleway.toString(Saleway.PACKAGING),
						idx: Saleway.PACKAGING
					},
					{
						text:Saleway.toString(Saleway.TAEOUT),
						idx: Saleway.TAEOUT
					},
					{
						text:Saleway.toString(Saleway.PICKUP),
						idx: Saleway.PICKUP
					}
				],
/*用餐方式*/

/*餐厅标签*/
				shoplabel: [],	
/*餐厅标签*/
/*发票分类*/
				inType:[],
				paper: [{
						text:Invoice.toString(Invoice.PLIANINVOICE),
						idx: Invoice.PLIANINVOICE
					},
					{
						text: Invoice.toString(Invoice.SPECIALINVOICE),
						idx: Invoice.SPECIALINVOICE
					}
				],
/*发票分类*/
				startsystem: 0, //控制启用 还是暂停系统 ， 默认是启用。
				shopsystem: {
					img_list:[]   //店铺图片
				},
	/*获取时间*/
				value2: '', //营业时间开始
				value3: '', //营业时间结束
				value4: '',//早市开始时间
				value5: '',//早市结束时间
				value6: '',//午市开始时间
				value7: '',//午市结束时间
				value8: '',//晚市开始时间
				value9: '',//晚市结束时间
				value10: '',//夜宵开始时间
				value11: '',//夜宵结束时间
    /*获取时间*/
				type:[],      //选中的餐时保存数组  如：早市
				remark: '',    //发票备注
				imgbase_url: imgbase_url , //图片路径
				opening_time:[], //餐时设置
				invoies:{    //发票
						is_invoice:Invoce.YES, //支持与不支持发票
						invoice_type:[]  //支持发票类型
				       },
				labelObj:{  //添加餐厅标签弹窗的配置
					name:'餐厅标签',
					value:'shop_label',
					limitNum: 30,
				    limitWord: 5,
				    limitSelect: 5
				},
				shoppic:[], //保存店铺图片
				imgSize:1024, //长传图片尺寸上限
				imgSrc:'',
				showImg:false
			};
		},
		created() {
			this.getData();  //调用请求数据
		},
		computed: {
			Invoce() {
				return Invoce;
			},
			Startsystem(){
				return Startsystem;
			},
			Payway(){
				return Payway;
			},
			PayTime(){
				return PayTime;
			},
			Saleway(){
				return Saleway;
			},
			Restauran(){
				return Restauran;
			},
			Invoice(){
				return Invoice;
			},
			Dinnertime(){
				return Dinnertime;
			}
		},
		methods: {
			//图片
		    getShopphoto(res) {
		    	if(this.shoppic.length>=5){
		    		return ;
		    	}
		    	this.shoppic.push(res);
		    	
		    },
		    delt(index){
		    	this.shoppic.splice(index,1)
		    },
		     //字符长度
    getLen(str) {
      if (str == null) return 0;
      if (typeof str !== "string") {
        str += "";
      }
      return str.replace(/[^\x00-\xff]/g, "01").length;
    },

    //remark check
    check() {
      if (this.getLen(this.remark) > 240) {
        this.$notify({
          title: "警告",
          message: "备注超过120字",
          type: "warning"
        });
        return true;
      }
    },
	//不提供发票点击事件
			shows() {
				this.show = Invoce.NO;
				this.showinvos = Invoce.NO;
				this.showinvs = Invoce.NO;
				this.showvos = Invoce.NO;
			},
	//系统按钮点击事件
			satrt(index) {
				this.startsystem = index;
			},
	//转换时间
			formatTime(t,n){
				if(n){
					return t.toString().split(' ')[4]
				}
				if(t){
					let arr = t.toString().split(' ')[4].split(':');
					return {
						"hh":arr[0],
						"mm":arr[1],
						"ss":arr[2]
					}
				}
			},
			
	//保存数据
			save() {
				if(this.pay.length===0){
					alert('支付方式必须选一个')
					return ;
					}
				if(this.timePay.length===0){
					alert('付款时间必须选一个')
					return ;
				}
				if(this.dinner.length===0){
					alert('销售方式必须选一个')
					return ;
				}
				if(Util.isEmpty(this.value2) || Util.isEmpty(this.value3)){
					alert('营业时间必须全部选择')
					return ;
				} 
				  if(this.value4 ==='' && this.value5 === '' && this.value6 === '' && this.value7 === ''
				
				  && this.value8 === '' && this.value9 === '' && this.value10 === '' && this.value11 === '' ){
				  	alert('餐时设置必须选一个')
					return ;
				}
				  if(this.check(this.remark)){
				  	return ;
				  }
				let {value2,value3,value4,value5,value6,value7,value8,value9,value10,value11} = this,
				opening_time = [],
				open_time = [this.formatTime(value2,1),this.formatTime(value3,1)];
				this.type.forEach(item=>{
					if(item==Dinnertime.MORN){
						opening_time.push({
							'type':Dinnertime.MORN,
							'from':{...this.formatTime(value4)},
							'to':{...this.formatTime(value5)}
						})
					}
					if(item==Dinnertime.NOON){
						opening_time.push({
							'type':Dinnertime.NOON,
							'from':{...this.formatTime(value6)},
							'to':{...this.formatTime(value7)}
						})
					}
					if(item==Dinnertime.EVEN){
						opening_time.push({
							'type':Dinnertime.EVEN,
							'from':{...this.formatTime(value8)},
							'to':{...this.formatTime(value9)}
						})
					}
					if(item==Dinnertime.NIGHT){
						opening_time.push({
							'type':Dinnertime.NIGHT,
							'from':{...this.formatTime(value10)},
							'to':{...this.formatTime(value11)}
						})
					}
				})
				if(this.show === Invoce.YES && this.showinvs!==Invoce.NO && this.inType.indexOf(this.showinvs)===-1){
					this.inType.push(this.showinvs)
				}
				this.invoies.invoice_type = this.inType;	
				let shop_id = Public.getShopid();
				let data = {
					shopinfo_save: 1,
					shop_id:shop_id,
					suspend: this.startsystem,
					is_invoice_vat:this.invoies,
					shop_pay_way: this.pay,
					pay_time: this.timePay,
					sale_way: this.dinner,
					shop_label: this.shoplabel,
					open_time,
					invoice_remark: this.remark,
					opening_time,
					img_list:this.shoppic
				};
				shopSeting(data, this.alts);
			},
			//保存数据成功的回调
			alts(resp) {
					if(resp.ret === 0) {
						this.$router.go(-1);
					}else {
						
				    }
			},					
/*餐时设置*/
			setTimes(arr,y,m,d){
				let type = [];
				arr.forEach(item=>{
					if(item.type==Dinnertime.MORN){
						type.push(Dinnertime.MORN)
						this.value4 = new Date(y, m, d, item.from.hh,item.from.mm,item.from.ss);
						this.value5 = new Date(y, m, d, item.to.hh,item.to.mm,item.to.ss);
					}else if(item.type==Dinnertime.NOON){
						type.push(Dinnertime.NOON)
						this.value6 = new Date(y, m, d, item.from.hh,item.from.mm,item.from.ss);
						this.value7 = new Date(y, m, d, item.to.hh,item.to.mm,item.to.ss);
					}else if(item.type==Dinnertime.EVEN){
						type.push(Dinnertime.EVEN)
						this.value8 = new Date(y, m, d, item.from.hh,item.from.mm,item.from.ss);
						this.value9 = new Date(y, m, d, item.to.hh,item.to.mm,item.to.ss);
					}else if(item.type==Dinnertime.NIGHT){
						type.push(Dinnertime.NIGHT)
						this.value10 = new Date(y, m, d, item.from.hh,item.from.mm,item.from.ss);
						this.value11 = new Date(y, m, d, item.to.hh,item.to.mm,item.to.ss);
					}
				})
				this.type = type
			},
/*餐时设置*/	
	//获取数据
			getData(){
					let _this = this;
					let t = new Date();
					let year = t.getFullYear();
					let mon = t.getMonth();
					let dats = t.getDate();
					getShopinfo({
					get_shopinfo_edit: 1
					}, function(resp) {
						if(resp){
							_this.shopsystem = resp.data.shopinfo||{};
						if(!Util.isEmpty(_this.shopsystem.opening_time)){
							_this.setTimes(_this.shopsystem.opening_time,year,mon,dats)
						}
					_this.Zindex = _this.shopsystem.suspend;
					if(!Util.isEmpty(_this.shopsystem.is_invoice_vat)){
						_this.invoies = _this.shopsystem.is_invoice_vat;
						_this.show = _this.invoies.is_invoice;
						_this.inType = _this.invoies.invoice_type;
					
					}
					
					if(!Util.isEmpty(_this.inType)){
						if(_this.inType.length>=3){    //如果长度大于等于3 就把电子发票从数组删除
						_this.showinvs = _this.inType.pop();
						}
						if(_this.inType.length===1 && _this.inType.indexOf(Invoce.ELECTRONICINVOICE)!==-1){//如果长度等于1并且第一个下标的数字是3改变纸质发票的选中状态 纸票子类的显示与隐藏
						_this.showinvs = _this.inType.pop();
						_this.showinvos = Invoce.NO;
						_this.showvos = Invoce.NO;
						}
						
						if(_this.inType.indexOf(Invoce.YES)!==-1||_this.inType.indexOf(Invoce.PAPERINVOICE)!==-1){//如果数组里面有1或2就改变纸质发票的选中状态
					 	_this.showinvos = Invoce.PAPERINVOICE;
					 }
					if(_this.show===Invoce.NO){
					 	_this.showinvos = Invoce.NO;
					 	_this.showvos = Invoce.NO;
					}	
				}
					_this.pay = _this.shopsystem.shop_pay_way;
					_this.timePay = _this.shopsystem.pay_time;
					_this.dinner = _this.shopsystem.sale_way;
					_this.shoplabel = _this.shopsystem.shop_label;
					_this.remark = _this.shopsystem.invoice_remark;
					if(!Util.isEmpty(_this.shopsystem.img_list)){
						_this.shoppic = _this.shopsystem.img_list;
					}
					if(!Util.isEmpty(_this.shopsystem.open_time)){
						let times = _this.shopsystem.open_time;
						if(times.length!==0){
							_this.value2 =	new Date(year, mon, dats, ...times[0].split(':'));
							_this.value3 =	new Date(year, mon, dats, ...times[1].split(':'));
						}	
					}
					
					}
					
				});
			},
			
	//跳转回店铺信息页面
			goBack() {
				this.$router.go(-1);
			},
	//纸质发票
			showInvos(){
				if(this.showinvos === Invoce.NO){
				    this.showinvos = Invoce.PAPERINVOICE;
					this.showvos = Invoce.SHOWPAPERINVOICE;
					this.show = Invoce.YES;
				}else{
					this.showinvos = Invoce.NO;
					this.showvos = Invoce.NO;
				}
			},
	//电子发票
			addCls(){
				if(this.showinvs === Invoce.NO ){
					this.showinvs = Invoce.ELECTRONICINVOICE;
					this.show = Invoce.YES;
				}else{
					this.showinvs = Invoce.NO;

				}
			},
	/*添加餐厅标签弹窗*/
			addlabel(){
				this.dialogVisible = true;
			},
			hideDialog() {
      			this.dialogVisible = false;
    		},
			getLabel(select){
				this.shoplabel = select;
			},
/*添加餐厅标签弹窗*/
			 clickImg(index) {
                this.showImg = true;
                // 获取当前图片地址
                this.imgSrc = this.shoppic[index];
           },
            viewImg(){
                this.showImg = false;
            }
	},
		watch:{
			show:function(newVal){
				this.invoies.is_invoice = newVal;
			}
		}
		
	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		position: relative;
		button {
			cursor: pointer;
			outline: none;
		}
		.active {
			background: #4877E7 !important;
			color: #fff !important;
			border-radius: 2px !important;
		}
		.selectd{
			color: #4877E7;
			background-color: #fff;
			border-color: #4877E7 !important;
			box-shadow: none;
			position: relative;
			&:after {
				position: absolute;
				right: 0;
				top: 0;
				content: '';
				@include wh(12px, 12px);
				background: url(../../img/select.png)no-repeat;
			}
		}
		.shopinfo{
			position: relative;
			@include wh(100px,100px);
			margin-right:30px;
			img{
				width: 100%;
				height: 100%;
				display: block;
				transition:opacity .3s;
			}
		}
		.shopinfo:hover .bg{
			position: absolute;
			left: 0;
			top: 0;
			@include wh(100px,100px);
			background: rgba(0,0,0,.5);
			cursor: pointer;
		}
		.label{
			position: relative;
			display: inline-block;
			@include wh(80px,30px);
			border: 1px solid #D8D8D8;
			border-radius: 2px;
			@include fc(14px,#4877E7);
			text-align: center;
			line-height: 30px;
			margin-right: 10px;
			border-color: #4877E7 !important;
			&:after {
				position: absolute;
				right: 0;
				top: 0;
				content: '';
				@include wh(12px, 12px);
				background: url(../../img/select.png)no-repeat;
			}
		}
		.add{
			display: inline-block;
			@include wh(24px,24px);
			background: url(../../img/add.png)no-repeat;
			vertical-align: middle;
			cursor: pointer;
		}
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		background:#fff;
		.title {
			text-indent: 14px;
			@include fc($sizes, $grey);
			height: 40px;
			line-height: 40px;
			background: #F6F8FC;
		}
		.shopSeting {
			margin: 40px 0 0 162px;
			.injection {
				@include fc(12px, #9b9b9b);
			}
			.start {
				@include wh(80px, 30px);
				line-height: 30px;
				@include fc(14px, #666);
				border: 1px solid #d8d8d8;
				background: #fff;
				&:first-child {
					margin-right: 10px;
				}
			}
			.line {
				float: left;
				display: inline-block;
				height: 30px;
				width: 1px;
				background: #d8d8d8;
				margin: 4px 20px 0 20px;
			}
			.paperInvoice,.electronicInvoice{
				@include wh(100px,30px);
				background: #fff;
				border: 1px solid #d8d8d8;
			}
			.paperInvoice{
				margin-right:6px;
			}
			.shopPic {
				@include wh(100px, 100px);
				background: #e1e1e1;
			}
			.busPic {
				@include wh(100px, 100px);
				background: #e1e1e1;
				img{
					width:100%;
					height:100%;
					display: block;
				}
			}
			.text-warp {
				position: absolute;
				left:0px;
				top: 60px;
				@include fc(12px, #9b9b9b);
				margin-top: 40px;
			}
			.picInfo {
				width: 54%;
				margin-left: 20px;
			}
			.noInvoice {
				@include wh(100px, 30px);
				line-height: 30px;
				@include fc(14px, #666);
				background: #fff;
				border: 1px solid #d8d8d8;
				margin-top: 4px;
			}
		}
		.bottom {
			width: 100%;
			text-align: center;
			padding-bottom: 50px;
			padding-top:40px;
			margin-left: 280px;
			.blue {
				@include fc(16px, #ffffff);
				background: #4877E7;
				@include wh(160px, 40px);
				line-height: 40px;
				border-radius: 6px;
			}
			.default {
				@include fc(14px, #4877E7);
				background: #ffffff;
				@include wh(160px, 40px);
				line-height: 40px;
				border-radius: 6px;
				border: 1px solid #4877E7;
				margin-left: 50px;
			}
		}
	}
</style>

